import React, { Component } from 'react';
import { connect } from 'react-redux';
import PropTypes from 'prop-types';

import Input from '../Input/Input';
import Button from '../Button/Button';
import TodoList from '../TodoList/TodoList';
import RadioButtons from '../RadioButtons/RadioButtons';

import * as actionTypes from '../../store/actions';

import './App.scss'

const TODO_FILTERS = [
  {
    label: 'all',
    value: 'all'
  }, {
    label: 'active',
    value: 'active'
  }, {
    label: 'completed',
    value: 'completed'
  }
]

export class App extends Component {
  render() {

    return (
      <div className="app">
        <Input
          inputValue={this.props.inputValue}
          onInputChange={(e) => this.props.changeInput(e)} 
          onInputValueChange={(e) => this.props.changeValue(e)}
          enterAction={() => this.props.addTodo(this.props.inputValue)}
          />
        <Button
          className='btn btn-add'
          disabled={this.props.isBtnDisabled}
          onButtonClick={() => this.props.addTodo(this.props.inputValue)}
          label='Add' />
        <RadioButtons
          todoFilters={TODO_FILTERS}
          formName='panel'
          onRadioButtonChange={this.props.setFilter} />
        <TodoList
          todos={this.props.todos}
          currentFilter={this.props.currentFilter}
          onDeleteBtnClick={this.props.deleteTodo}
          onCompleteBtnClick={this.props.toggleCompletedTodo} />
      </div >
    )
  }
}

App.propTypes = {
  addTodo: PropTypes.func.isRequired,
  deleteTodo: PropTypes.func.isRequired,
  todos: PropTypes.array.isRequired,
  inputValue: PropTypes.string.isRequired,
  changeInput: PropTypes.func.isRequired,
  changeValue: PropTypes.func.isRequired,
  setFilter: PropTypes.func.isRequired,
  toggleCompletedTodo: PropTypes.func.isRequired,
  currentFilter: PropTypes.string.isRequired,
  isBtnDisabled: PropTypes.bool.isRequired
}

const mapStateToProps = state => {
  return { 
    todos: state.todos, 
    currentFilter: state.currentFilter, 
    inputValue: state.inputValue,
    isBtnDisabled: state.isBtnDisabled
  }
}

const mapDispatchToProps = dispatch => {
  return {
    changeInput: (e) => dispatch({ type: actionTypes.CHANGE_INPUT, e: e }),
    changeValue: (e) => dispatch({type: actionTypes.CHANGE_VALUE, e: e}),
    addTodo: (inputValue) => dispatch({ type: actionTypes.ADD, inputValue: inputValue }),
    deleteTodo: (id) => dispatch({ type: actionTypes.DELETE, id: id }),
    toggleCompletedTodo: (id) => dispatch({ type: actionTypes.COMPLETE, id: id }),
    setFilter: (value) => dispatch({ type: actionTypes.SET_FILTER, filterValue: value })
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(App);


