import uniqueId from 'uuid';
import * as actionTypes from './actions';
import { updateObject } from './utils';

const initialState = {
	todos: [],
	inputValue: '',
	currentFilter: 'all',
	isBtnDisabled: true
}

const changeInput = (state, action) => {
	return updateObject(state, {
		inputValue: action.e.target.value
	})
};

const changeValue = (state, action) => {
	return updateObject(state, {
		isBtnDisabled: action.e.target.value.length > 0 ? false : true
	})
};

const addTodo = (state, action) => {
	const todo = {
		value: action.inputValue,
		id: uniqueId(),
		completed: false
	}
	return updateObject(state, {
		todos: state.todos.concat(todo),
		inputValue: '',
		isBtnDisabled: true
	})
};

const deleteTodo = (state, action) => {
	return updateObject(state, {
		todos: state.todos.filter(todo => (todo.id !== action.id))
	})
};

const completeTodo = (state, action) => {
	return updateObject(state, {
		todos: state.todos.map(todo => (todo.id === action.id ? { ...todo, completed: !todo.completed } : todo))
	})
};

const setFilter = (state, action) => {
	return updateObject(state, {
		currentFilter: action.filterValue
	})
};

const reducer = (state = initialState, action) => {

	switch (action.type) {

		case actionTypes.CHANGE_INPUT:
			return changeInput(state, action);
		case actionTypes.CHANGE_VALUE:
			return changeValue(state, action);
		case actionTypes.ADD:
			return addTodo(state, action);
		case actionTypes.DELETE:
			return deleteTodo(state, action);
		case actionTypes.COMPLETE:
			return completeTodo(state, action);
		case actionTypes.SET_FILTER:
			return setFilter(state, action);
		default:
			return state;
	}
}

export default reducer;


