import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';

import Button from '../Button/Button';

import './Todo.scss';

class Todo extends Component {

  deleteHandler = () => this.props.onDeleteBtnClick(this.props.id);
  completeHandler = () => this.props.onCompleteBtnClick(this.props.id);

  render() {

    const todoClassName = cx({
      'todo': true,
      'todo--completed': this.props.completed
    })

    return <li className={todoClassName}>
      <Button
        className='btn todo-btn todo-btn-complete'
        label='complete'
        onButtonClick={this.completeHandler} />
      {this.props.label}
      <Button
        className='btn todo-btn todo-btn-delete'
        label='delete'
        onButtonClick={this.deleteHandler} />
    </li>
  }
}

Todo.propTypes = {
  label: PropTypes.string.isRequired,
  id: PropTypes.string.isRequired,
  completed: PropTypes.bool.isRequired,
  onDeleteBtnClick: PropTypes.func.isRequired,
  onCompleteBtnClick: PropTypes.func.isRequired
}

export default Todo;


